body {
  margin: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(to top, #accbee 0%, #e7f0fd 100%);
  font-family: "Jua", sans-serif;
}
.title {
  padding-top: 50px;
  text-align: center;
}
.movie__poster {
  width: 230px;
}
.movie__year__input {
  font-family: "Jua", sans-serif;
  letter-spacing: 2px;
  font-size: 15px;
  width: 170px;
  height: 30px;
  padding-left: 10px;
  border-radius: 5px;
  outline: none;
  border: none;
}
.movie__year__button {
  font-family: "Jua", sans-serif;
  font-size: 15px;
  width: 42px;
  height: 35px;
  margin-left: 5px;
  background-color: rgb(0, 0, 0);
  color: white;
  border-radius: 5px;
  position: relative;
  top: -1px;
  outline: none;
  border: none;
}
.correct_count b {
  color: blue;
  font-size: 25px;
}
